/* jshint ignore:start */

if (!runningTests) {
  require("wge/app")["default"].create({"name":"wge","version":"0.0.0+ac6eb75c"});
}

define('~fastboot/app-factory', ['wge/app', 'wge/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
